.main-processing {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  font-size: 18px
}

#div-processing-banner-wrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 8px 64px 64px 64px 8px;
  grid-gap: 8px;
}

#div-processing-banner-wrapper section {
  grid-row: 3;
  grid-column: 3 / 5;
  -ms-flex-item-align: start;
      align-self: flex-start;
  padding: 12px 6px;
  border: 2px #777700 solid;
  border-radius: 8px 0 0 8px;
  border-right: 0;
  text-align: center;
  text-shadow: 1px 1px 16px #444444;
  background: #FFFFBB;
  color: #222222;
  opacity: 0.75
}

#div-cattle-image {
  grid-row: 2 / 5;
  grid-column: 1 / 4;
  border: 0 #777777 solid;
  border-radius: 0 8px 8px 0;
  border-width: 2px 2px 2px 0;
  background: url(../../assets/images/banner-cattle.jpg);
  background-position: center 40%;
  background-repeat: no-repeat;
  background-size: cover
}

.main-processing > section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 288px;
  margin: 8px;
  border-radius: 4px;
  border: 1px #555555 solid;
  -webkit-box-shadow: #777777 1px 1px 2px;
          box-shadow: #777777 1px 1px 2px;
  background: #B5651D44;
  font-size: 16px;
  overflow: hidden;
}

.main-processing h5 {
  text-align: center;
  margin: 16px auto
}

.main-processing p {
  width: 90%;
  max-width: 720px;
  margin: 32px auto;
  text-align: justify
}

#div-button-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 90%;
  max-width: 320px;
  margin: 0 auto;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center
}

#img-barbed-wire {
  width: 75%;
  max-width: 360px;
  height: auto;
  margin: 8px auto;
  display: block
}

.main-processing .a-pdf-link:link,
.main-processing .a-pdf-link:visited {
  width: 120px;
  margin: 0 32px;
  color: #222222;
  background: goldenrod;
  border: 2px solid #777777;
  border-radius: 4px;
  text-decoration: none;
  -webkit-transition: all 0.25s;
  -o-transition: all 0.25s;
  transition: all 0.25s
}

.main-processing .a-pdf-link:hover,
.main-processing .a-pdf-link:active {
  background: orange
}

@media screen and (max-width: 720px) {
  #div-processing-banner-wrapper section {
    grid-column: 2 / 5;
    font-size: 14px
  }
}


