.main-home {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  overflow: hidden
}

.main-home h4 {
  margin: 0 auto;
  text-align: center
}

#div-home-banner-wrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 8px 64px 64px 64px 8px;
  grid-gap: 8px;
}

#div-home-banner-wrapper section {
  grid-row: 3;
  grid-column: 1 / 3;
  -ms-flex-item-align: start;
      align-self: flex-start;
  padding: 12px 6px;
  border: 2px #777700 solid;
  border-radius: 0 8px 8px 0;
  border-left: 0;
  text-align: center;
  text-shadow: 1px 1px 16px #444444;
  background: #FFFFBB;
  color: #222222;
  opacity: 0.75
}

#div-corn-field-image {
  grid-row: 2 / 5;
  grid-column: 2 / 5;
  border: 0 #777777 solid;
  border-radius: 8px 0 0 8px;
  border-width: 2px 0 2px 2px;
  background: url(../../assets/images/banner-corn.jpg);
  background-position: center 40%;
  background-repeat: no-repeat;
  background-size: cover
}

.main-home p {
  width: 75%;
  max-width: 720px;
  margin: 16px auto;
  text-align: justify
}

#img-barbed-wire {
  width: 75%;
  max-width: 360px;
  height: auto;
  margin: 8px auto;
  display: block
}

@media screen and (max-width: 720px) {
  #div-home-banner-wrapper section {
    grid-column: 1 / 4;
    font-size: 12px
  }
}


